	module.exports = {"data":{"markdownRemark":{"html":"<p><em>This page is under continuous edits</em> Continuation from part two. Random mixed bag o' stuff.</p>\n<h1>Difficult Situations</h1>\n<h2>Conflict Management</h2>\n<ul>\n<li>‚ùì General question about how to work through disagreements and conflicts </li>\n</ul>\n<p>In general, stay calm and respectful. Try to slowly win by examples, by explaining, showing, or demonstrating specific cases.</p>\n<ul>\n<li>‚ùì Pair programming, convincing senior developer with your ideas</li>\n</ul>\n<p>Similar to the how would you deal with conflicting ideas question. Specifically say if you're working on a project together with a senior colleague and he or she is adamant about his or her solution even though you know that yours have more merit?</p>\n<p>Calm, communicate, and compromise. Sacrifice a little but stand your ground. Slowly win their trust by showing that you're reliable and dependable.</p>\n<ul>\n<li>‚ùì Give specific example about disagreements where you have very strong opinions about</li>\n</ul>\n<h2>Old Employers</h2>\n<ul>\n<li>‚ùì What would your old boss say about you, how would they rate you</li>\n</ul>\n<h2>Deadlines</h2>\n<ul>\n<li>‚ùì How would you deal with missing deadlines</li>\n<li>‚ùì Tell me about a time where you miss a deadline and the resolution</li>\n<li>‚ùì How do you deal with getting stuck or hitting a wall</li>\n<li>‚ùì What do you do when there is a downtime in between development cycles</li>\n<li>‚ùì How do you request more time for a project</li>\n<li>‚ùì Tell me about a time when something went wrong and how its handled</li>\n<li>‚ùì What did you dislike about your last manager</li>\n</ul>\n<h1>Hobbies</h1>\n<ul>\n<li>‚ùì What other non technical interest do you have</li>\n<li>‚ùì Walk me through your most interesting side project</li>\n<li>‚ùì What did you build with your best language</li>\n</ul>\n<h1>Basics</h1>\n<ul>\n<li>‚ùì What interests you, what do you do for fun</li>\n<li>‚ùì What motivates you to go to work everyday</li>\n<li>‚ùì What makes you happy</li>\n<li>‚ùì What are your greatest strengths, greatest weakness</li>\n<li>‚ùì What do you do at work, how is your work? </li>\n<li>‚ùì Under 1 minute tell me about yourself</li>\n<li>‚ùì What are your next steps, goals</li>\n<li>‚ùì Have you mentored before? Tell me about your mentoring experience</li>\n<li>‚ùì What does getting it right the first time mean to you</li>\n<li>‚ùì Ask the interviewer anything: questions about the company, product, engineering culture</li>\n</ul>","frontmatter":{"title":"üìï Behavioral Q&A Cheatsheet Hypothetical Scenarios","date":"03 February, 2018","language":null,"categories":"questions","carousel":null}}},"pathContext":{"slug":"/posts/note/2018-03-20-behavioral-part-3/"}}

